<template>
  <div class='TempalteDetaile'>
    <el-card>
    <el-tabs v-model="activeName">
      <el-tab-pane name="show" label="展示">
        <el-table :data="tableData">
          <el-table-column type="expand">
            <template #default="o">
              <el-col :span="12">
                <el-form label-width="80px" :model="o.row" :rules="rules" ref="editForm">
                  <el-form-item label="名称" prop="areas">
                    <el-input v-model="o.row.areas"></el-input>
                  </el-form-item>
                  <el-form-item label="价钱" prop="price">
                    <el-input v-model="o.row.price"></el-input>
                  </el-form-item>      
                  <el-form-item label="提交">
                    <el-button type="primary" @click="handleEdit(o.row)">修改</el-button>
                  </el-form-item>
                </el-form>
              </el-col>
            </template>
          </el-table-column>

            <el-table-column label="区域信息" prop="areas" align="center"></el-table-column>
            <el-table-column label="价格" prop="price" align="center">
              <template slot-scope="scope">{{scope.row.price|currency}}</template>
            </el-table-column>
            <el-table-column label="操作" align="center">
              <template slot-scope="scope">
                <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
      </el-tab-pane>
      <el-tab-pane name="add" label="添加">
        <el-col :span="12">
            <el-form :model="form" label-width="80px" :rules="rules" ref="form">
              <el-form-item label="配送区域" prop="areas">
                <el-input v-model="form.areas" style="width:300px"></el-input>
              </el-form-item>
              <el-form-item label="配送价格" prop="price">
                <el-input v-model="form.price" style="width:300px"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="handleAdd">添加</el-button>
              </el-form-item>
            </el-form>
          </el-col>
      </el-tab-pane>
    </el-tabs>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'TempalteDetaile',
  data(){
    return {
      // 查询地址
      url:"/Ddetail",
      // 展示与添加
      activeName:"show",
      // 表格数据
      tableData:[],
      // 添加的表单
      form:{
        areas:'',
        price:'',
        did: this.did
      },
      // 编辑的数据
      editForm: {},
      rules: {
        areas: { required: true, message: "请输入配送区域", trigger: "blur" },
        price: { required: true, message: "请输入配送价格", trigger: "blur" }
      }
    }
  },
  props: {
        did: {
          type:Number,
          required: true,
        }
      },
  components: {},
  created(){},
  mounted(){
    this.fetchData();
  },
  methods: {
    // 数据获取
        fetchData(){
          console.log(this.did)
      this.$http.get(this.url,{params: {did:this.did}}).then(res=>{
        if (res.data.code===200){
          this.tableData = res.data.data;
        }
      })
    },
   // 添加数据
   // 添加数据操作
    handleAdd(){
      this.$refs.form.validate(v=>{
        if (v){
          this.$http.post(this.url,this.form).then(res=>{
            if (res.data.code==200) {
              this.$message.success("添加成功");
              this.fetchData();
              // 清空表单数据
              this.$refs.form.resetFields();
              this.$refs.form.areas = '';
              this.$refs.form.price  = '';
              // 跳转到查看表格
              this.activeName = "show";
              // 重新获取数据 
            }
          })
        }else {
              this.$message.error('添加失败')
        }
      })
    },
   // 数据修改
   handleEdit(obj){
      console.log(obj);
      this.$refs.editForm.validate(v=>{
        if (v){
          delete obj.create_time;
          delete obj.update_time;
          this.$http.put(this.url+'/'+obj.id,obj).then(res=>{
            if (res.data.code ==200){
              this.$message.success("修改成功");
              this.fetchData();
            }else {
              this.$message.error("修改失败");
            }
          })
        }else {
          this.$message.error("请根据提示输入");
        }
      });
    },
    // 删除数据
    handleDelete(id){
       this.$confirm("此操作将删除当前配送详情,是否继续？","提示",{
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      }).then(()=>{
        this.$http.delete(this.url+"/"+id).then(res=>{
          if (res.data.code ==200){
            this.$message.success("删除成功");
            // 重新获取数据
            this.fetchData();
          }
        });
      }).catch(() => {
        this.$message({
          type:"info",
          message:"已取消删除 "
        });
      });
    }
  },
  watch: {
    did() {
      console.log(this.did)
      this.form.did = this.did;
      this.fetchData()
    }
  }
}
</script>
<style lang='scss' scoped>
.el-card {
  margin-top: 20px;
  margin-left: 20px;
  // height: 450px;
}
</style>
