<template>
  <div class="ddetail">
    <!-- 面包屑  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path:'/main'}">配送管理</el-breadcrumb-item>
      <el-breadcrumb-item>配送详情</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 展示数据 -->
    <el-card>
      <!-- 选项卡模式 -->
      <el-tabs v-model="activeName">
        <!-- 查看表格列表 -->
        <el-tab-pane label="查看" name="show">
          <el-table :data="tableData">
            <el-table-column label="基本ID" prop="id" align="center"></el-table-column>
            <el-table-column label="配送模板id" prop="did" align="center"></el-table-column>
            <el-table-column label="区域信息" prop="areas" align="center"></el-table-column>
            <el-table-column label="价格" prop="price" align="center">
              <template slot-scope="scope">{{scope.row.price|currency}}</template>
            </el-table-column>
            <el-table-column label="配送模板名" prop="dtemplate.name" align="center"></el-table-column>
            <el-table-column label="创建时间" prop="create_time" align="center"></el-table-column>
            <el-table-column label="修改时间" prop="update_time" align="center"></el-table-column>
            <el-table-column label="操作" align="center">
              <template slot-scope="scope">
                <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-row type="flex" justify="center" align="middle" style="height:40px">
            <el-pagination
              background
              :current-page.sync="page"
              :page-size.sync="pageSize"
              layout="total"
              :total="total"
            ></el-pagination>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="添加" name="add">
          <el-col :span="12">
            <el-form :model="addForm" label-width="80px" :rules="rules" ref="addForm">
              <el-form-item label="配送模板" prop="did">
                <el-autocomplete
                  class="inline-input"
                  v-model="name"
                  :fetch-suggestions="querySearch"
                  placeholder="请输入模板选择"
                  :trigger-on-focus="false"
                  @select="handleSelect"
                ></el-autocomplete>
              </el-form-item>
              <el-form-item label="配送区域" prop="areas">
                <el-input v-model="addForm.areas" style="width:300px"></el-input>
              </el-form-item>
              <el-form-item label="配送价格" prop="price">
                <el-input v-model="addForm.price" style="width:300px"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="handleAdd">添加</el-button>
              </el-form-item>
            </el-form>
          </el-col>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <el-dialog title="编辑模板详细信息" :visible.sync="dialogVisible" width="50%">
      <el-form :model="editForm" label-width="80px" :rules="rules" ref="editForm">
        <el-form-item label="配送模板" prop="did">
          <el-autocomplete
            class="inline-input"
            v-model="editName"
            :fetch-suggestions="querySearch"
            placeholder="请输入模板选择"
            :trigger-on-focus="false"
            @select="handleSelectEdit"
          ></el-autocomplete>
        </el-form-item>
        <el-form-item label="配送区域" prop="areas">
          <el-input v-model="editForm.areas" clearable style="width:300px"></el-input>
        </el-form-item>
        <el-form-item label="配送价格" prop="price">
          <el-input type="number" v-model="editForm.price" clearable style="width:300px"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="handleUpdate()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "ddetail",
  data() {
    return {
      // 模板选项卡
      activeName: "show",
      // 表格数据
      tableData: [],
      // 弹框默认false
      dialogVisible: false,
      // 地址
      url: "/ddetail",
      // 模板名称
      name: "",
      // 编辑跳出的绑定的模板
      editName: "",
      // 分页
      page: 1,
      // 展示分页数量
      pageSize: 6,
      // 总条数
      total: 0,
      // 添加的数据
      addForm: {
        did: "",
        areas: "",
        price: ""
      },
      // 编辑的数据
      editForm: {},
      rules: {
        did: [
          { required: true, message: "请选择模板", trigger: "blur" },
          {
            type: "number",
            message: "请通过选择的方式来指定模板",
            trigger: "blur"
          }
        ],
        areas: { required: true, message: "请输入配送区域", trigger: "blur" },
        price: { required: true, message: "请输入配送价格", trigger: "blur" }
      }
    };
  },
  components: {},
  created() {},
  mounted() {
    this.fetchData();
  },
  methods: {
    // 获取数据列表
    fetchData() {
      this.$http
        .get(this.url, {
          params: {
            // 用get请求需要使用params里去传送数据 用data的数据
            page: this.page,
            pageSize: this.pageSize
          }
        })
        .then(res => {
          if (res.data.code === 200) {
            console.log(res);
            this.tableData = res.data.data;
            this.total = res.data.total;
            // this.tableData.ereas = res.data.data.ereas;
            // console.log(this.tableData.ereas);
          } else {
            this.$message.error("获取失败");
          }
        });
    },
    // 搜索配送模板
    querySearch(queryString, cb) {
      this.$http
        .get("/dtemplate", {
          params: {
            page: 1,
            pageSize: 100,
            name: queryString
          }
        })
        .then(res => {
          let dtemplate = res.data.data;
          let options = dtemplate.map(v => ({
            value: v.name,
            id: v.id
          }));
          cb(options);
        });
    },
    // 查询之后得到的模板id
    handleSelect(res) {
      this.addForm.did = res.id;
      this.$refs.addForm.validateField("did");
    },
    // 编辑查询之后得到的模板id
    handleSelectEdit(res) {
      this.editForm.did = res.id;
      this.$refs.editForm.validateField("did");
    },
    // 添加模板详细信息
    handleAdd() {
      this.$refs.addForm.validate(v => {
        if (v) {
          this.$http.post(this.url, this.addForm).then(res => {
            if (res.data.code === 200) {
              this.$message.success("模板添加成功");
              this.$refs.addForm.resetFields();
              this.activeName = "show";
              this.fetchData();
            } else {
              this.$message.error("添加失败");
            }
          });
        }
      });
    },
    // 删除模板信息
    handleDelete(id) {
      this.$confirm("此操作将删除当前模板详情, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          this.$http.delete(this.url + "/" + id).then(res => {
            if (res.data.code === 200) {
              this.$message.success("删除成功");
              this.fetchData();
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 点击编辑弹出编辑页面
    handleEdit(obj) {
      this.editForm = obj;
      this.$http.get("/dtemplate/" + obj.did).then(res => {
        if (res.data.code === 200) {
          this.editName = res.data.data.name;
        } else {
          this.$message.error("数据获取失败");
        }
      });
      this.dialogVisible = true;
    },
    // 确认提交编辑操作
    handleUpdate() {
      this.$refs.editForm.validate(v => {
        if (v) {
          this.$http
            .put(this.url + "/" + this.editForm.id, this.editForm)
            .then(res => {
              if (res.data.code === 200) {
                // 提示成功
                this.$message.success("编辑成功");
                // 关闭弹窗
                this.dialogVisible = false;
                //  重新获取数据
                this.fetchData();
              } else {
                this.$message.error("编辑失败");
              }
            });
        } else {
          this.$message.error("请按要求输入");
        }
      });
    }
  },
  watch: {
    // 监测页的变化
    page(newValue, oldValue) {
      this.fetchData();
    },
    // 监测条数的变化
    pageSize(newValue, oldValue) {
      this.fetchData();
    }
  }
};
</script>
<style lang='scss' scoped>
.el-breadcrumb {
  line-height: 60px;
}
.el-card {
  margin-top: 20px;
  margin-left: 20px;
  // height: 450px;
}
.avatar-uploader .el-upload {
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
  border: 1px dashed #d9d9d9;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.el-table {
  margin-top: 30px;
}
.el-form {
  margin-top: 30px;
}
// .el-input__inner{
//   width:200px!important;
// }
</style>
