<template>
  <div class="dtemplate">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path:'/ddetail'}">配送管理</el-breadcrumb-item>
      <el-breadcrumb-item>配送模板</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-tabs v-model="activeName">
        <el-tab-pane label="查看" name="show">
          <el-table :data="tableData">
            <el-table-column label="ID" prop="id" align="center"></el-table-column>
            <el-table-column label="模板名" prop="name" align="center">
              <!-- <el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree> -->
            </el-table-column>
            <el-table-column label="创建时间" prop="create_time" align="center"></el-table-column>
            <el-table-column label="修改时间" prop="update_time" align="center"></el-table-column>
            <el-table-column label="操作" align="center">
              <template slot-scope="scope">
                <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)">删除</el-button>
              </template>
            </el-table-column>
            <el-table-column label="配送管理" align="center">
              <template #default="o">
                <el-button size="mini" @click="handleDetailEdit(o.row.id)">配送详情</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="添加" name="add">
          <el-col :span="12">
            <el-form label-width="80px" :model="addForm" :rules="rules" ref="addForm">
              <el-form-item label="模板名称" prop="name">
                <el-input v-model="addForm.name"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="handleAdd">添加</el-button>
              </el-form-item>
            </el-form>
          </el-col>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <el-dialog title="编辑模板详细信息" :visible.sync="dialogVisible" width="50%">
      <el-form :model="editForm" label-width="80px" :rules="rules" ref="editForm">
        <el-form-item label="模板名称" prop="name">
          <el-input v-model="editForm.name" clearable style="width:300px"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="handleUpdate()">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="编辑配送详情" :visible.sync="dialogVisible3" width="70%">
      <template-detail :did="did" @success="dialogVisible3=false"></template-detail>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible3=false">取消</el-button>
        <!-- <el-button type="primary" @click="handleUpdate()">确认</el-button> -->
      </span>
    </el-dialog>
  </div>
</template>

<script>
import TemplateDetail from "@/components/TemplateDetail";
export default {
  name: "dtemplate",
  data() {
    return {
      activeName: "show",
      tableData: [],
      url: "/dtemplate",
      page: 1,
      pageSize: 3,
      total: 0,
      addForm: {
        name: ""
      },
      rules: {
        name: { required: true, message: "请输入模板名称", trigger: "blur" }
      },
      // data:this.res.data.data.name,
      // defaultProps: {
      //     children: 'children',
      //     label: 'tableData.name'
      // }
      // 弹窗
      dialogVisible: false,
      // 配送详情的弹窗
      dialogVisible3: false,
      // 编辑的数据
      editForm: {},
      // 配送详情的did
      did: 0
    };
  },
  components: {
    TemplateDetail
  },
  created() {},
  mounted() {
    this.fetchData();
  },
  methods: {
    // 查看数据
    fetchData() {
      this.$http
        .get(this.url, {
          params: {
            page: this.page,
            pageSize: this.pageSize
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            this.tableData = res.data.data;
            this.total = res.data.total;
          } else {
            this.$message.error("查询失败");
          }
        });
    },
    handleAdd() {
      this.$refs.addForm.validate(v => {
        if (v) {
          this.$http.post(this.url, this.addForm).then(res => {
            if (res.data.code == 200) {
              this.$message.success("添加成功");
              this.$refs.addForm.resetFields();
              this.activeName = "show";
              this.fetchData();
            } else {
              this.$message.error("添加失败");
            }
          });
        }
      });
    },
    handleNodeClick(data) {
      console.log(data);
    },
    // 打开编辑弹窗
    handleEdit(obj) {
      this.dialogVisible = true;
      this.editForm = obj;
    },
    // 编辑
    handleUpdate() {
      this.$refs.editForm.validate(v => {
        if (v) {
          delete this.editForm.create_time;
          delete this.editForm.update_time;
          this.$http
            .put(this.url + "/" + this.editForm.id, this.editForm)
            .then(res => {
              if (res.data.code === 200) {
                // 提示成功
                this.$message.success("编辑成功");
                // 关闭弹窗
                this.dialogVisible = false;
                //  重新获取数据
                this.fetchData();
              } else {
                this.$message.error("编辑失败");
              }
            });
        } else {
          this.$message.error("请按要求输入");
        }
      });
    },
    // 配送详情弹窗
    handleDetailEdit(id) {
      console.log(id)
      this.dialogVisible3 = true;
      this.did = id;
    }
  },
  watch: {
    page: function(newValue) {
      this.fetchData();
    },
    pageSize: function(newValue) {
      this.fetchData();
    }
  }
};
</script>
<style lang='scss' scoped>
</style>
